/* overview.scss */
.overview-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px;
    padding: 10px;
    border: 1px solid black;
  }
  
  .overview-button {
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .overview-count {
    margin: 0 10px;
  }
  